.card {
  padding-top: 67px;
}

.card-content {
  padding-top: 20px;
  &__bottom {
    padding-top: 70px;
  }
}

.card-top{
  .container {
    display: flex;
  }

  &__left {
    width: 57.5%;
  }
  &__right {
    padding-top: 57px;
    padding-left: 10.7%;
    width: calc(100% - 57.5%);
  }
}

.card-info {
  &__prop {
    padding-left: 19px;
    margin-bottom: 16px;
  }

  &__title {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 500;
    font-size: 24px;
  }

  &__rate {
    margin-bottom: 23px;
    color: var(--color-gray);
    font-size: 13px;
    img {
      margin-right: 3px;
    }
  }

  &__price {
    margin-bottom: 22px;
  }

  &__descr {
    margin-bottom: 52px;
    font-size: 13px;
    line-height: 20px;
    color: var(--color-tundora);
  }

  &__first-select {
    margin-bottom: 32px;
  }
  &__second-select {
    margin-bottom: 82px;
  }

  &__stepper {
    margin-bottom: 10px;
  }

  &__btn {
    margin-bottom: 10px;
    width: 100%;
    height: 58px;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    background-color: #f2f2f2;
    color: var(--color-dark);
    transition: all 0.3s;

    &:hover {
      transition: all 0.3s;
      background-color: var(--color-dark);
      color: var(--color-white);
    }

    &--tocart {
      background-color: var(--color-dark);
      color: var(--color-white);

      &:hover {
        transition: all 0.3s;
        background-color: #f2f2f2;
        color: var(--color-dark);
      }
    }
  }
}

.info-price {
  display: flex;
  align-items: flex-end;

  &__current {
    margin-right: 20px;
    font-weight: 700;
    font-size: 24px;
  }

  &__old {
    font-size: 16px;
    color: #959595;
    text-decoration: line-through;
  }
}

.color-select {
  &__selected {
    margin: 0;
    margin-bottom: 14px;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    color: var(--color-gray);
    span {
      display: inline-block;
      margin-left: 3px;
      color: var(--color-dark);
    }
  }

  &__list {
    display: flex;
    align-items: center;
    padding-left: 6px;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 13px;
    }
  }

  &__btn {
    position: relative;
    width: 24px;
    height: 24px;
    border-radius: 100%;
    box-shadow: 0px 0px 3px var(--color-gray);

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      width: 32px;
      height: 32px;
      border: 1px solid #ccc;
      border-radius: 100%;
      transform: translate(-50%, -50%);
      transition: all 0.3s;
    }

    &--active,&:hover {
      &::before {
        transition: all 0.3s;
        border: 1px solid var(--color-dark);
      }
    }
  }
}


.size-select {
  &__selected {
    margin: 0;
    margin-bottom: 13px;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    color: var(--color-gray);
    span {
      display: inline-block;
      margin-left: 3px;
      color: var(--color-dark);
    }
  }

  &__reset {
    display: none;
    align-items: center;
    color: #d24242;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;

    svg {
      margin-right: 4px;
      width: 9px;
      height: 9px;
      fill: #d24242;
    }
  }

  &__list {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 5.5px;
    }
  }

  &__btn {
    position: relative;
    width: 42px;
    height: 32px;
    font-size: 13px;
    color: var(--color-gray);
    transition: all 0.3s;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border: 1px solid #ccc;
      transition: all 0.3s;
    }

    &--active,&:hover {
      color: var(--color-dark);
      transition: all 0.3s;

      &::before {
        transition: all 0.3s;
        border: 1px solid var(--color-dark);
      }
    }
  }
}

.stepper {
  display: flex;
  height: 58px;
  border: 1px solid #ccc;


  &__input {
    text-align: center;
    flex-grow: 1;
    border: none;
    height: 100%;

    color: var(--color-dark);
    font-size: 13px;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 100%;

    &--disabled {
      opacity: 0.4;
      pointer-events: none;
    }

    svg {
      width: 12px;
      height: 12px;
      fill: var(--color-gray);
      transition: all 0.3s;
    }

    &:hover {
      svg {
        fill: var(--color-dark);
      }
    }

  }
}

.card-bottom {
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
  flex-wrap: wrap;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  color: var(--color-dark);

  span {
    color: var(--color-gray);
  }

  &__social {
    display: flex;
    align-items: center;
    margin-top: 15px;

    span {
      margin-right: 13px;
    }
  }
}


.card-description {
  display: flex;
  align-items: flex-start;

  &__left {
    padding-top: 30px;
    width: 16%;
  }

  .simplebar-track.simplebar-vertical {
    width: 3px;
    background-color: #ccc;
  }
  .simplebar-scrollbar::before {
    opacity: 1;
    width: 3px;
    height: 100%;
    background-color: var(--color-dark);
    border-radius: 0;
    left: 0;
    right: 0;
  }

  &__right {
    padding-top: 22px;
    padding-left: 125px;
    padding-right: 120px;
    width: 84%;
  }

  &__navigation {
    padding: 23px 0;
    width: 100%;
    max-height: 210px;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 50px;
    }
  }

  &__link {
    font-weight: 700;
    font-size: 15px;
    color: var(--color-gray);

    &--active {
      color: var(--color-dark);
      pointer-events: none;
    }
  }

  &__content {
    display: none;

    p {
      margin: 0;
      margin-bottom: 30px;
      font-size: 13px;
      color: var(--color-dark);
      line-height: 26px;
    }

    &--active {
      display: block;
    }
  }
}

.card-related {

  padding-top: 40px;

  &__title {
    margin-bottom: 40px;
    font-weight: 500;
    font-size: 24px;
    line-height: 26px;
  }

  &__slider {
    padding-bottom: 90px;
    position: relative;
    overflow: hidden;
  }
}

.related-pag {
  left: 0;
  bottom: 0;
  text-align-last: left;
  .swiper-pagination-bullet {
    margin: 0 9px !important;
    width: 6px;
    height: 6px;
    background-color: #ccc;
    opacity: 0.5;
  }

  .swiper-pagination-bullet-active {
    position: relative;
    background-color: var(--color-dark);
    opacity: 1;

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      width: 20px;
      height: 20px;
      border-radius: 20px;
      background-color: #ccc;
      transform: translate(-50%, -50%);
      z-index: -1;
    }
  }
}
